<template>
  <div class="film-box" style="height:100%">
    <!-- style="height:100%" -->
    <div class="content" :style="{ minHeight: curHeight+'px'}" >
      <div style="padding-top: 1rem;">
        <!--  -->
        <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
          <!-- <nav-head :title="title" ></nav-head> -->
          <nav-head :title="languageStatus =='zh' ? '':'Set transaction password'" ></nav-head>
        </div>
          <!--  -->
          <!--    -->
        <div class="person-card-box">
            <van-field v-if="languageStatus=='en'" v-model="locapass" label="old password:" placeholder="Enter the original password" type="password" />
            <van-field v-else v-model="locapass" label="  " placeholder="" type="password" />
        </div>
          <!--    -->
        <div class="person-card-box" style="margin-top: 0">
            <van-field v-if="languageStatus=='en'" v-model="paypass1" label="new password:" placeholder="Enter a new password" type="password"   />
            <van-field v-else v-model="paypass1" label="  " placeholder="" type="password"   />
        </div>
          <!--  -->
        <div class="person-card-box" style="margin-top: 0">
            <van-field v-if="languageStatus=='en'" v-model="paypass2" label="confirm password:" placeholder=" New password again" type="password"  />
            <van-field v-else v-model="paypass2" label="" placeholder="" type="password"  />
        </div>
          <!--  -->
        <div class="bottom-btn" @click="userBut()">{{$t('common.baocunxinimima')}}</div>
          <!-- tips -->
        <div class="bottom-tips">
            <span>{{$t('common.warning_tips2')}}</span>
            <!-- <span style="color: #04B9FF"></span> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { paypassword } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import Vue from 'vue'
import NavHead from '@/components/head.vue'
import { Field, Toast } from 'vant'
Vue.use(Field).use(Toast)
// axios.defaults.baseURL = '192'
export default {
  components: { NavHead },
  data () {
    return {
      languageStatus: '',
      value: '',
      url: this.GLOBAL.domainName,
      title: '',
      paypasswordData: {
        u_id: '10',
        paypass: '',
        locapass: ''
      },
      paypass1: '', // 
      paypass2: '', // 
      locapass: '', // 
      curHeight: '',
      transaction_password: '' // 
    }
  },
  created () {
    // name.replace(/\s+/g,);
    this.paypasswordData.u_id = sessionStorage.getItem('u_id')
    this.transaction_password = sessionStorage.getItem('transaction_password')
    this.beforeMount()
    // this.paypasswordFun()
    this.getLanguageStatus()
  },
  methods: {
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },
    userBut () {
      var that = this
      var pay1 = Number(this.paypass1.replace(/\s+/g, ''))
      var pay2 = Number(this.paypass2.replace(/\s+/g, ''))
      if (that.transaction_password === null || that.transaction_password === '') {
        that.paypasswordData.locapass = 0
      } else {
        if (that.locapass === '') {
          that.$toast({ message: '', duration: 1500 })
          return
        }
      }
      if (pay1 === '') {
        that.$toast({ message: '', duration: 1500 })
        return
      }
      if (pay2 === '') {
        that.$toast({ message: '', duration: 1500 })
        return
      }
      if (pay1 !== pay2) {
        that.$toast({ message: '', duration: 1500 })
        return
      }
      var locapass1 = Number(this.locapass.replace(/\s+/g, ''))
      that.paypasswordData.locapass = locapass1
      that.paypasswordData.paypass = that.paypass1
      that.paypasswordFun()
      // if
    },
    paypasswordFun () {
      var that = this
      var data = objKeySort(this.paypasswordData)
      var token = sgin(data)
      this.paypasswordData.token = token
      paypassword(this.paypasswordData).then(res => {
        console.log(res, that, '')
        if (res.code === 0) {
          that.$toast({ message: '', duration: 1500 })
          sessionStorage.setItem('transaction_password', that.locapass)
          that.paypass1 = ''
          that.paypass2 = ''
          that.locapass = ''
          delete this.paypasswordData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.paypasswordData.locapass = ''
        that.paypasswordData.paypass = ''
        delete this.paypasswordData.token
      })
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    }
  }
}
</script>
<style scoped>
.content{
    background-color: #000;
}
  .film-box{
    position: relative;
  }
  .viewRecord{
    position: absolute;
    right: 2rem;
    top: 2rem;
    z-index: 20;
    background: #fff;
    border-radius: 100%;
  }
  .collection{
    position: absolute;
    right: 2.5rem;
    top: 2rem;
    z-index: 20;
    background: #fff;
    border-radius: 100%;
  }
  /* ***************** */
.nav-bar{
    height: 1.28rem;
    line-height: 1.28rem;
    display: flex;
    padding: 0 0.3rem;
    overflow: hidden;
    background:rgba(27,33,59,1);
}
.left{
    flex: 1;
    text-align: left;
    /* margin-top: 0.2rem; */
}
.nav-bar img{
    vertical-align: middle;
}
.center{
    flex: 1;
    /* margin-top: 0.2rem; */
    font-size: 0.36rem;
    color: rgba(255,255,255,1);
}
.right{
    flex: 1;
    text-align: right;
    /* margin-top: 0.2rem;   */
}
.person-card-box{
    background-color: #202641;
    margin-top: 0.2rem;
    padding: 0.3rem 0;
}
.person-card-box >>>.van-cell{
width:6.9rem;
height:0.8rem;
/* background:#12172B; */
background:#13172B;
border-radius:0.1rem;
margin: 0 auto;
}
.person-card-box >>>.van-cell{
font-size: 0.28rem;
color: rgba(255,255,255,0.6)!important;
}
.person-card-box >>> input::-webkit-input-placeholder{
            color: rgba(255,255,255,0.3);
        }

.person-card-box >>> .van-cell__title span{
    color: rgba(255,255,255,0.3);
    font-size: 0.28rem;
    font-family:Source Han Sans CN;
    font-weight:400;

}
.bottom-btn{
    width:6.9rem;
height:0.8rem;
line-height: 0.8rem;
color: rgba(255,255,255,0.7);
background:linear-gradient(-90deg,rgba(16,99,255,1),rgba(2,197,255,1));
border-radius:0.1rem;
margin: 0 auto;
margin-top: 0.4rem;
}
.bottom-tips{
    display: flex;
    justify-content: space-between;
    font-size: 0.24rem;
    color: rgba(255,255,255,0.3);
    padding: 0 0.3rem;
    margin-top: 0.27rem;
}
.person-card-box >>> .van-field__control{
  color: rgba(255,255,255,0.5) !important;
}
</style>
